import React, { Component } from 'react'
import axios from 'axios'
//import ClienteRest from '../clienteRest'
//https://github.com/FaztWeb/mern-crud-2019/blob/master/backend/src/app.js


const URL_SERVER = "https://jl.tocomple.cl:8181"
export default class CreateCargo extends Component {

   
    state = {
        cargos: []
    }


    async componentDidMount (){
        const res = await axios.get(URL_SERVER + '/api/cargos');
        this.setState({cargos: res.data.data});
        console.log(this.state.cargos);


    }



    render() {
        return (
            <div>
                <div className="row">
                <div className="col-md-8">
                    <ul className="list-group">
                   
                    {
                            this.state.cargos.map(cargos => 
                                <li className="list-group-item list-group-item-action" key={cargos.cargo_id} >
                                    {cargos.nombre}
                                </li>
                            )
                        }
                    </ul>
                </div>
            </div>
                
            </div>
        )
    }
}
