import React from 'react';
// contenedor lo renombro , rutas
import {BrowserRouter as Router,Route} from 'react-router-dom'
import 'bootstrap/dist/css/bootstrap.min.css'
import './App.css';



import Navigation from './components/Navigation'
import CargoList from './components/CargosList'
import CrearCargo from './components/CreateCargo'
import CreateUser from './components/CreateUser'

function App() {
  return (
   <Router>
      <Navigation/>

    <Route exact path="/"  component={CargoList} />
    <Route exact path="/edit/:id" component={CrearCargo} />
    <Route exact path="/create" component={CrearCargo} />
    <Route exact path="/user" component={CreateUser} />
   </Router>
  );
}

export default App;
